<template>
	<h4 id="title-wrap">
		<span class="title" name="title">{{titles.title}}</span>
		<span class="sub-title" name="sub-title" :style="{color: titles.subColor}">{{titles.subtitle}}</span>
	</h4>
</template>
<script>
	export default{
		props:{
			titles: {
				Object
			}
		}
	}
</script>
<style lang="less">
	
	@media (min-width: 767px) {
		#title-wrap{
			position: absolute;
			top: 80px;
			left: 60px;
			width: 80%;
			font-size: 40px;
			.title{
				font-weight: bolder;
				color: #fff;
			}
			.sub-title{
				position: absolute;
				top: 20px;
				left: 80px;
				font-size: 20px;
			}
		}
	}
	@media (max-width: 767px) {
		#title-wrap{
			position: absolute;
			top: 3rem;
			left: 1rem;
			width: 80%;
			font-size: 1.4rem;
			.title{
				font-weight: bolder;
				color: #fff;
			}
			.sub-title{
				position: absolute;
				top: 0.8rem;
				left: 1.2rem;
				font-size: 0.8rem;
			}
		}
	}
</style>


