<template>
	<div id="page1" class="page">
		<div class="content" :style="linHeight">
			<p>
				<span>H</span>
				<span>e</span>
				<span>l</span>
				<span>l</span>
				<span>o</span>
			</p>
			<p>
				<span>W</span>
				<span>o</span>
				<span>r</span>
				<span>l</span>
				<span>d</span>
				<span>!</span>
			</p>
		</div>
		<a class="fa fa-angle-down arrow-down" @click="nextPage"></a>
	</div>
</template>
<script>
	import  'font-awesome/css/font-awesome.css'
	export default {
		data() {
			return {
				linHeight: {
				  lineHeight: `${document.documentElement.clientHeight}px`
				}
			}
		},
		methods: {
			nextPage() {
				this.$emit('arrow')
			}
		}
	}
</script>
<style lang="less">

	@keyframes in {
		form {
			transform: translateX(1000px)
		}
		to{
			transform: translateX(0px)
		}
	}
	@keyframes bigShake {
		form {
			transform: scale(3)
		}
		20% {
			transform: scale(3)
		}
		40%{
			transform: scale(1)
		}
		50% {
			transform-origin: left;
			transform: rotate(120deg)
		}
		60% {
			transform-origin: left;
			transform: rotate(20deg)
		}
		80% {
			transform-origin: left;
			transform: rotate(120deg)
		}
		to{
			transform-origin: left;
			transform: rotate(0)
		}
	}
	@keyframes rotateLoop{
		form{
			color: rgba(255, 255, 255, 0);
			transform: rotate(0deg);
		}
		70%{
			color: rgba(255, 255, 255, 1);
			transform: rotate(360deg);
		}
		to{
			color: rgba(255, 255, 255, 0);
			transform: rotate(360deg);
		}
	}
	#page1{
		position: relative;
		background-color: #098;
		color: #fff;
		font-weight: bolder;
		overflow: hidden;
		text-align: center;
		.content{
			display: inline-block;
			line-height: 100px;
			animation: bigShake 3s 3s ease-in-out forwards;
			p{
				display: inline-block;
				margin-right: 30px;
				font-size: 0;
				span{
					display: inline-block;
					
					transform: translateX(1000px);
					@media (min-width: 767px) {
						font-size: 100px;
					}
					@media (max-width: 767px) {
						font-size: 40px;
					}
				}
			}
			p:first-child{
				span:first-child{
					animation: in .4s ease-in-out forwards;
				}
				span:nth-child(2){
					animation: in .3s .4s ease-in-out forwards;
				}
				span:nth-child(3){
					animation: in .2s .7s ease-in-out forwards;
				}
				span:nth-child(4){
					animation: in .1s .9s ease-in-out forwards;
				}
				span:last-child{
					animation: in .3s 1s ease-in-out forwards;
				}
			}
			p:last-child{
				span:first-child{
					animation: in .3s 1.3s ease-in-out forwards;
				}
				span:nth-child(2){
					animation: in .3s 1.6s ease-in-out forwards;
				}
				span:nth-child(3){
					animation: in .2s 1.9s ease-in-out forwards;
				}
				span:nth-child(4){
					animation: in .1s 2.1s ease-in-out forwards;
				}
				span:nth-child(5){
					animation: in .2s 2.2s ease-in-out forwards;
				}
				span:last-child{
					animation: in .3s 2.5s ease-in-out forwards;
				}
			}
		}
		.arrow-down{
			position: absolute;
			left: 0;
			bottom: -20px;
			width: 100%;
			padding: 20px 0;
			text-align: center;
			font-size: 40px;
			color: rgba(255, 255, 255, 0);
			animation: rotateLoop 4s 6s ease-in infinite;
			cursor: pointer;
		}
	}
	

	
	
</style>


