<template>
	<div id="page3" class="page">
		<Title class="title-wrapper" :titles="titles"></Title>
		<div class="content-warp">
			<div class="page-left">
			  <ul class="fight-list">
			    <li class="fight-item" v-for="item in user.skill">
			      <i class="fa fa-heart" aria-hidden="true"></i>
			      {{item}}
			    </li>
			  </ul>
			</div>
			<div class="page-right">
			  <ul class="round-wrapper">
			    <li class="round-item" v-for="i in this.round" 
			      :style="{width: i.size,height: i.size,top: i.y,left:i.x,lineHeight: i.size}">
			      {{i.text}}
			    </li>
			  </ul>
			</div>
		</div>
	</div>
</template>
<script>
	import Title from '@/components/title.vue'
	export default {
	  data() {
	    return {
	      round:[],
	      titles: {
					title: '战斗力',
					subtitle: 'Fighting force',
					subColor: '#1068d4'
				}
	    }
	  },
	  props: {
	    user: {
	      Object
	    }
	  },
	  beforeMount(){
	    let rounds = [
	        {text: 'PS',size:50},
	        {text: 'AI',size:100},
	        {text: 'CAD',size:40},
	        {text: 'HTML5',size:100},
	        {text: 'CSS3',size:100},
	        {text: 'ES6',size:70},
	        {text: 'Less',size:70},
	        {text: 'JQuery',size:100},
	        {text: 'EasyUI',size:70},
	        {text: 'Bootstrap',size:70},
	        {text: 'Vue',size:60},
	        {text: 'webpack',size:70},
	        {text: 'Node',size:60},
	        {text: 'Mongodb',size:70},
	      ];
	    for(let i of rounds){
	      let x = Math.floor(Math.random()*500)
	      let y = Math.floor(Math.random()*600)
	      i.x = x
	      i.y = y
	    }
	    for(let i of rounds){
	        for(let j of rounds){
	          if (Math.abs(i.x - j.x) <10) {
	            j.x += Number(j.size)/2
	          };
	          if (Math.abs(i.y - j.y) <10) {
	            j.y -= Number(j.size)/2
	          };
	        }
	        for(let j of rounds){
	          if (Math.abs(i.x - j.x) <15) {
	            j.x -= Number(j.size)/2
	          };
	          if (Math.abs(i.y - j.y) <15) {
	            j.y += Number(j.size)/2
	          };
	        }
	        i.x =`${i.x}px`
	        i.y =`${i.y}px`
	        i.size =`${i.size}px`
	    }
	    this.round = rounds
	  },
	  components: {
	  	Title
	  }
	}
</script>
<style lang="less">
	#page3{
		position: relative;
		background-color: #6adef9;

		.descript{
			position: absolute;
			left: 60px;
			top: 80px;
			width: 80%;
			font-size: 40px;
			.title{
				font-weight: bolder;
				color: #fff;
			}
			.subtitle{
				position: absolute;
				top: 20px;
				left: 80px;
				font-size: 20px;
				color: #1068d4;
			}
		}
		@media (min-width: 767px) {
			.content-warp{
				width: 1200px;
				margin: 0 auto;
				padding-top: 160px;
				display: flex;
				.page-left{
					flex: 0 0 50%;
				  .fight-list{
				    .fight-item{
				      margin-top: 20px;
				      i{
				        color: #fff;
				        width: 30px;
				      }
				      &:hover i{
				          color: #900;
				        }
				    }
				  }
				}
				.page-right{
					flex: 0 0 50%;
				  .round-wrapper{
				    position: relative;
				    width: 100%;
				    height: 100%;
				    top: -120px;
				    .round-item{
				      position: absolute;
				      background-color: rgba(106, 170, 249, 0.6);
				      border-radius: 50%;
				      color: #fff;
				      text-align: center;
				    }
				  }
				}
			}
		}
		@media (max-width: 767px) {
			.content-warp{
				width: 90%;
				margin: 0 auto;
				padding-top: 5rem;
				.page-left{
				  .fight-list{
				    .fight-item{
				    	font-size: 0.8rem;
				    	margin-top: 0.2rem;
				    	line-height: 1.2rem;
				      i{
				        color: #fff;
				        width: 20px;
				      }
				      &:hover i{
				          color: #900;
				        }
				    }
				  }
				}
				.page-right{
				  .round-wrapper{
				  	display: none;
				    position: relative;
				    width: 100%;
				    height: 100%;
				    top: -120px;
				    .round-item{
				      position: absolute;
				      background-color: rgba(106, 170, 249, 0.6);
				      border-radius: 50%;
				      color: #fff;
				      text-align: center;
				    }
				  }
				}
			}
		}
		

	}
</style>


