<template>
	<nav>
		<div class="left">
			<span>柳雅慧</span>
		</div>
		<div class="right">
			<p class="pc">
				<span @click="jumping(0)">关于我</span>
				<span @click="jumping(1)">战斗力</span>
				<span @click="jumping(2)">成长历程</span>
				<span @click="jumping(3)">项目经验</span>
				<span @click="jumping(4)">设计</span>
				<span @click="jumping(5)">作品集</span>
			</p>
			<p class="min">
				<button class="min-more"><i class="fa fa-align-right" @click="minMore"></i></button>
				<ul class="min-list" v-if="minShow">
					<li @click="jumping(0)">关于我</li>
					<li @click="jumping(1)">战斗力</li>
					<li @click="jumping(2)">成长历程</li>
					<li @click="jumping(3)">项目经验</li>
					<li @click="jumping(4)">设计</li>
					<li @click="jumping(5)">作品集</li>
				</ul>
				
			</p>
		</div>
		
		
	</nav>
</template>
<script>
	export default{
		data() {
			return {
				minShow: false
			}
		},
		methods: {
			jumping(index) {
				this.$emit('jump', index);
				this.minShow = false
			},
			minMore() {
				this.minShow = !this.minShow;
			}
		}
	}
</script>
<style lang="less">
	nav{
		position: fixed;
		z-index: 99;
		display: flex;
		width: 100%;
		background-color: rgba(34, 34, 34, 0.9);
		@media (min-width: 767px) {
			>div{
				&.left{
					flex: 0 0 50%;
					span{

						padding-left: 20px;
						font-size: 20px;
						box-sizing: border-box;
					}
				}
				&.right{
					flex:1;
					.min{
						display: none;
					}
					.pc{
						display: flex;
						span{
							flex: 1;
							display: inline-block;
							font-size: 16px;
							&:hover{
								color: #999;
							}
						}
					}
					
				}
				span{
					color: #fff;
					height: 50px;
					line-height: 50px;
					text-align: center;
					cursor: pointer;
					font-weight: 800;
				}

			}
		}
		@media (max-width: 767px) {
			>div{
				&.left{
					width: 100%;
					span{
						padding-left: 20px;
						font-size: 1rem;
						line-height: 40px;
						box-sizing: border-box;
					}
				}
				&.right{
					.pc{
						display: none;
					}
					.min{
						position: relative;
						.min-more{
							border: none;
							background-color: transparent;
							.fa{
								padding-top: 6px;
								font-size: 26px;
								color: #09dca1;
							}
						}
						.min-list{
							position: absolute;
							top: 50px;
							right: 0;
							background-color: rgba(34, 34, 34, 0.9);
							&::before{
								position: absolute;
								top: -12px;
								right: 10px;
								width: 0;
								height: 0;
								content: '';
								border-width: 6px;
								border-style: solid;
								border-left-color: transparent;
								border-top-color: transparent;
								border-right-color: transparent;
								border-bottom-color:  rgba(34, 34, 34, 0.9);
							}
							li{
								width: 100px;
								padding: 0 10px;
								line-height: 40px;
								font-size: 1rem;
								color: #eee;
							}
						}
					}
				}
				span{
					color: #fff;
					height: 50px;
					line-height: 50px;
					text-align: center;
					cursor: pointer;
					font-weight: 800;
				}
			}
		}
		
		
	}
</style>


