				titles: {
					title: '关于我',
					subtitle: 'about me',
					subColor: '#098'
				},
				scrollTop: 0,
				windoHeight: document.documentElement.clientHeight,
				fixed: false
			}

		},
		mounted() {
			window.addEventListener('scroll', this.scroll)
		},
		components: {
			Title
		},
		methods: {
			scroll() {
				this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
  			if (this.scrollTop >= this.windoHeight) {
  				this.fixed = true
  			}else{
  				this.fixed = false
  			}

			}
		}
	}
