<template>
  <div id="page6" class="page">
    <Title class="title-wrapper" :titles="titles"></Title>
    <div class="banner-wrapper">
      <banner :listImg="listImg" class="banner"></banner>
    </div>
  </div>
</template>
<script>
  import banner from '@/components/banner.vue';
  import Title from '@/components/title.vue'
  export default{
    data() {
      return {
        listImg: [{
            url: 'static/images/1.png'
        }, {
            url: 'static/images/2.jpg'
        }, {
            url: 'static/images/3.jpg'
        }, {
            url: 'static/images/4.jpg'
        }, {
            url: 'static/images/5.jpg'
        },{
            url: 'static/images/6.png'
        }],
        titles: {
          title: '设计',
          subtitle: 'design',
          subColor: '#b9a303'
        }
      }
    },
    props: {
      user: {
        Object
      }
    },
    components:{
      banner,
      Title
    }
  }
</script>
<style lang="less">
  #page6{
    position: relative;
    background-color: #f5e574;
    h1{
        display: block;
        font-size: 60px;
        color: #fff;
        font-weight: bolder;
        padding: 40px;
        span{
          color: #d7d7d7;
          font-size: 18px;
          position: relative;
          font-weight: 500;
          left: -50px;
          top: 10px;
        }
      }
    @media (min-width: 767px) {
      .banner-wrapper{
        width: 80%;
        margin: 0 auto;
        padding-top: 10rem;
      }
    }
    @media (max-width: 767px) {
      .banner-wrapper{
        width: 80%;
        margin: 0 auto;
        padding-top: 5rem;
      }
    }
    
  }
</style>


