<template>
	<div class="main">
		<page1 :style="size" @arrow="showContainter"></page1>
		<div id="containter" :style="size">
			<Nav class="headerNav" @jump="jumping" :class="[fixed ? 'pose-fixed' : 'pose-absolute']"></Nav>

			<page2 :style="size" :about="self"></page2>
			<page3 :style="size" :user="user"></page3>
			<page4 :style="size" :user="user"></page4>
			<page5 :user="user"></page5>
			<page6 :style="size" :user="user"></page6>
			<Footer :style="size" :user="user"></Footer>
		</div>
		
	</div>
</template>
<script>
	import datas from '../../data.json'
	import Nav from '@/components/nav.vue'
	import page1 from '@/components/page1.vue'
	import page2 from '@/components/page2.vue'
	import page3 from '@/components/page3.vue'
	import page4 from '@/components/page4.vue'
	import page5 from '@/components/page5.vue'
	import page6 from '@/components/page6.vue'
	import Footer from '@/components/footer.vue'
	export default {
		data() {
			return {
				isShow: false,
				self: datas.user.self,
				user: datas.user,
				size: {
				  width: `100%`,
				  height: `${document.documentElement.clientHeight}px`,
				},
				windowHeight: document.documentElement.clientHeight,
				scrollTop: 0,
				fixed: false
			}
		},
		mounted() {
			window.addEventListener('scroll', this.scroll)
		},
		methods:{
			showContainter() {
				this.isShow = true;
				let i = 0;
				let t = 0;
				let timer = setInterval(function () {
					t += 2;
					i +=(10*t)
					if (document.documentElement.clientHeight>i) {
						document.documentElement.scrollTop = i
					}else{
						document.documentElement.scrollTop = document.documentElement.clientHeight
						clearInterval(timer)
					}
				}, 30)
			},
			scroll() {
				this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
  			if (this.scrollTop >= this.windowHeight) {
  				this.fixed = true
  			}else{
  				this.fixed = false
  			}
			},
			jumping (index) {
			    let jump = containter.querySelectorAll('.page')
			    console.log(jump[index].id)
			    // 获取需要滚动的距离
			    let total = jump[index].offsetTop + this.windowHeight
			    // Chrome
			    document.body.scrollTop = total
			    // Firefox
			    document.documentElement.scrollTop = total
			    // Safari
			    window.pageYOffset = total
			},
		},
		components: {
			Nav,
			page1,
			page2,
			page3,
			page4,
			page5,
			page6,
			Footer
		}
	}
</script>
<style lang="less">
	#containter{
		position: relative;
		.pose-absolute{
			position: absolute;
			top: 0;
			left: 0;
		}
		.pose-fixed{
			position: fixed;
			top: 0;
			left: 0;
		}
	}
		
	
</style>


