<template>
	<div id="page5" class="page"  :style="size">
		<Title class="title-wrapper" :titles="titles"></Title>
		<div class="content-wrapper">
			<div class="page-left">
			  <div class="box" v-for="(item,index) in user.project" v-if="index>1">
			    <div class="title">
			      <h2 class="name">{{item.name}}</h2>
			      <ul class="skill-list">
			        <span class="skill-item" v-for="i in item.skill">{{i}}</span>
			      </ul>
			    </div>
			    <a :href="item.adress" v-if="item.adress">{{item.adress}}</a>
			    <div class="duty">{{item.duty}}</div>
		    
		  	</div>
		  </div>
		  <div class="page-right">
		    <div class="box" v-for="(item,index) in user.project" v-if="index<2">
		      <div class="title">
		        <h2 class="name">{{item.name}}</h2>
		        <ul class="skill-list">
		          <span class="skill-item" v-for="i in item.skill">{{i}}</span>
		        </ul>
		      </div>
		      <a :href="item.adress" v-if="item.adress">{{item.adress}}</a>
		      <div class="duty">{{item.duty}}</div>
		    </div>
		  </div>
		</div>
	</div>
</template>
<script>
 	import Title from '@/components/title.vue'
	export default{
		data() {
			return {
				titles: {
					title: '项目经验',
					subtitle: 'Project experience',
					subColor: '#a00202'
				},
				size: {
				  width: `100%`,
				  // height: `${document.documentElement.clientHeight}px`,
				},
			}
		},
		props: {
			user: {
				Object
			}
		},
		components: {
			Title
		}
	}
</script>
<style lang="less">
	

	@media (min-width: 767px) {
		#page5{
		position: relative;
		background-color: #ff8484;
		.content-wrapper{
			width: 1200px;
			margin: 0 auto;
			padding-top: 120px;
			display: flex;
			.page-left{
				flex: 1;
			  h1{
			    display: block;
			    font-size: 60px;
			    color: #fff;
			    font-weight: bolder;
			    padding: 40px 40px 0 40px;
			    span{
			      color: #098;
			      font-size: 18px;
			      position: relative;
			      font-weight: 500;
			      left: -100px;
			      top: 10px;
			    }
			  }
			}
			.page-right{
				flex: 1;
			}
			.box{
	      width: 90%;
	      background-color: #f74f4f;
	      margin: 8% 3%;
	      padding: 2%;
	      border-radius: 20px;
	      .title{
	        margin: 6px 0;
	        h2{
	          display: inline-block;
	          font-size: 18px;
	          font-weight: bold;
	          margin-right: 4px;
	          color: #000;
	        }
	        .skill-list{
	          display: inline-block;

	        .skill-item{
	            border-radius: 4px;
	            margin-left: 2px;
	            padding: 2px 4px;
	            color: #fff;
	            background-color: rgba(202, 34, 34, 0.6);
	          }
	        }
	        a{
	        	color: #fdefef;
	        }
	      }
	      .description{
	         font-size: 14px;
	        .description-title{
	          font-weight: 500;
	        }
	        .description-text{
	          line-height: 18px;
	          margin-top: 5px;
	          text-indent: 28px;
	        }
	      }
	      a{
	        display: block;
	        padding-top: 4px;
	        text-align: right;
	        color: #fbe8e8;
	      }
	      .duty{
	        margin-top: 10px;
	        font-size: 16px;
	        line-height: 24px;
	        text-indent: 32px;
	      }
	    }
		}
	}
	}
	@media (max-width: 767px) {
		#page5{
			position: relative;
			background-color: #ff8484;
			.content-wrapper{
				width: 100%;
				margin: 0 auto;
				padding-bottom: 20px;
				padding-top: 4rem;
				.page-right{
				}
				.box{
		      width: 92%;
		      background-color: #f74f4f;
		      margin: 20px auto 0;
		      padding: 2%;
		      border-radius: 20px;
		      .title{
		        margin: 6px 0;
		        h2{
		          display: inline-block;
		          font-size: 1rem;
		          font-weight: bold;
		          margin-right: 4px;
		          color: #000;
		        }
		        .skill-list{
		          display: inline-block;
							margin-top: 0.5rem;
			        .skill-item{
			        	font-size: 0.8rem;
		            border-radius: 4px;
		            margin-left: 2px;
		            padding: 2px 4px;
		            color: #fff;
		            background-color: rgba(202, 34, 34, 0.6);
		          }
		        }
		        a{
		        	font-size: 0.6rem;
		        	color: #fdefef;
		        }
		      }
		      .description{
		         font-size: 14px;
		        .description-title{
		          font-weight: 500;
		        }
		        .description-text{
		          line-height: 1rem;
		          margin-top: 5px;
		          text-indent: 28px;
		        }
		      }
		      a{
		        display: block;
		        padding-top: 4px;
		        text-align: right;
		        color: #fbe8e8;
		        font-size: 0.9rem;
		      }
		      .duty{
		        margin-top: 0.4rem;
		        font-size: 0.8rem;
		        line-height: 1rem;
		        text-indent: 2rem;
		      }
		    }
			}
		}
	}
</style>


